import Button from "react-bootstrap/Button";

import React, { useState } from "react";
import { useEffect } from "react";
import { run as runHolder } from "holderjs/holder";
import Pools from "./json/pools";

import Accordion from "react-bootstrap/Accordion";

import "bootstrap/dist/css/bootstrap.css";

import "./cards.css";

export default function MinerPrice() {
  const [MinerPrices, initMinerPrices] = useState([]);
  let tempArray = [];
  const miners = {
    name: "",
    rate: 0,
    id: 0,
    ratio: 0,
    price: 0,
    saleid: 0,
    img: "",
    rarity: "",
  };

  const fetchData = async () => {
    const response = await fetch(
      "https://wax.api.atomicassets.io/atomicmarket/v1/sales/templates?symbol=WAX&collection_name=upliftworld&schema_name=miners&page=1&limit=50&order=desc&sort=template_id"
    );
    if (!response.ok) {
      throw new Error("Data coud not be fetched!");
    } else {
      return response.json();
    }
  };
  useEffect(() => {
    fetchData()
      .then((res) => {
        initMinerPrices(res.data);
      })
      .catch((e) => {
        console.log(e.message);
      });
  }, []);

  let j = 0;
  //console.log(MinerPrices);
  MinerPrices.forEach((e, i) => {
    //console.log(e);
    const filtered = Pools.data.payload.filter((obj) => {
      return obj.template_id === e.assets[0].template.template_id;
    });

    const miner = Object.create(miners);

    miner.saleid = e.sale_id;

    miner.name = filtered[0].label;
    miner.rate = parseFloat(filtered[0].size_per_tick_per_asset);
    miner.id = filtered[0].template_id;
    miner.img = e.collection.img

    if (e.listing_symbol !== "WAX") {
      miner.ratio = (
        filtered[0].size_per_tick_per_asset / e.listing_price
      ).toFixed(2);
    } else {
      miner.ratio = (
        (filtered[0].size_per_tick_per_asset / e.listing_price) *
        100000000
      ).toFixed(2);
    }

    tempArray[j] = miner;
    j++;

  });

  useEffect(() => {
    runHolder("image-class-name");
  });
 //console.log(tempArray);
  return (
    <div className="card-align">
      <Accordion style={{ width: "20rem" }}>
        <Accordion.Item eventKey="0">
          <Accordion.Header>
            Best miner deal (upliftium per hour to price ratio)
          </Accordion.Header>
          <Accordion.Body>
            {[...tempArray]
              .sort((a, b) => b.ratio - a.ratio)
              .map((item, idx) => {
                //let amount = item[1].toLocalString();
                return (
                  <p key={idx} id="p-spacing">
                    <a
                      id="card-link"
                      rel="noreferrer"
                      href={
                        "https://wax.atomichub.io/market/sale/" + item.saleid
                      }
                      target="_blank"
                    >
                      {" "}
                      {item.name} ({item.ratio})
                    </a>
                  </p>
                );
              })}
            <a
              href="https://moddedoutlaw.github.io/minerdeals/"
              target="_blank"
              rel="noreferrer"
            >
              <Button
                variant="primary"
                style={{ backgroundColor: "#210051", padding: "0em" }}
              >
                Go to MinerDeals
              </Button>
            </a>
          </Accordion.Body>
        </Accordion.Item>
      </Accordion>
    </div>
  );
}
