import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import NavDropdown from "react-bootstrap/NavDropdown";
import { NavLink } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./nav.css";

function NavigationBar() {
  return (
    <Navbar expand="lg" bg="bg" variant="dark">
      <Container>
        <Navbar.Brand as={NavLink} to="/rewards">
          <img
            alt="mio nft and liftium"
            className="nav-img"
            src={require("./img/eco.png")}
          />
          <span id="brand-tag">Uplift Community Project</span>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <NavDropdown title="Rewards Leaderboard" id="basic-nav-dropdown">
              <NavDropdown.Item as={NavLink} to="/leadersW1">
                S7 Week 1
              </NavDropdown.Item>
              <NavDropdown.Item as={NavLink} to="/leadersW2">
                {" "}
                S7 Week 2
              </NavDropdown.Item>
              <NavDropdown.Item as={NavLink} to="/leadersW3">
                S7 Week 3
              </NavDropdown.Item>

              <NavDropdown.Item as={NavLink} to="/leadersW4">
                S7 Week 4
              </NavDropdown.Item>
              <NavDropdown.Item as={NavLink} to="/leadersW5">
                S7 Week 5
              </NavDropdown.Item>
              <NavDropdown.Item as={NavLink} to="/leadersW6">
                S7 Week 6
              </NavDropdown.Item>
              <NavDropdown.Item as={NavLink} to="/leadersS8W1">
                S8 Week 1
              </NavDropdown.Item>
              <NavDropdown.Item as={NavLink} to="/leadersS8W2">
                S8 Week 2
              </NavDropdown.Item>
            </NavDropdown>

            <NavDropdown title="Reward Details" id="basic-nav-dropdown">
              <NavDropdown.Item as={NavLink} to="/rewarddetailsS8W1">
                S8 Week 1
              </NavDropdown.Item>
              <NavDropdown.Item as={NavLink} to="/rewarddetailsS8W2">
                S8 Week 2
              </NavDropdown.Item>
           
            </NavDropdown>
            <NavDropdown title="Mio/Liftium Stats" id="basic-nav-dropdown">
              <NavDropdown.Item as={NavLink} to="/liftium">
                Liftium Leaders
              </NavDropdown.Item>
              <NavDropdown.Item as={NavLink} to="/mio">
                Mio Leaders
              </NavDropdown.Item>
            </NavDropdown>
            <Nav.Link as={NavLink} to="/stores">
              Item Prices/Locations
            </Nav.Link>
            <Nav.Link style={{}}as={NavLink} to="/rewardcaps">
              Rewards System
            </Nav.Link>


           
         
          </Nav>
         
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default NavigationBar;
